<template>
  <div>
    <h1>Hello user-{{$route.params.id}}</h1>
    <h1>Hello user-{{par}}</h1>
    <h1>Hello! C-{{user.id}}</h1>
    <h2>{{user.name}}</h2>
    <h2>{{user.age}}</h2>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'User',
  props: ['id'],
  data () {
    return {
      user: {},
      par: 0
    }
  },
  created () {
    /*this.$http.get(`api/statuses`)
      .then(function (response) {
        this.statuses = response.data
      })*/
      this.par = this.$route.params.id
      axios.get('/api/get-user', {
          params: {
            id : this.$route.params.id
          }
        })
        .then((response) => {
          next(vm => {
            vm.user = response.data
          })
        })
  }/*,
  beforeRouteEnter (to, from, next) {
    // this.$http.get(`api/hello`)
    axios.get('/api/get-user', {
        params: {
          id : id
        }
      })
      .then((response) => {
        next(vm => {
          vm.user = response.data
        })
      })
  }*/
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
    color : red;
  }
</style>



