import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Service from '@/components/Service'
import Bootstrap from '@/components/Bootstrap'
import Board from '@/components/Board'
import Card from '@/components/Card'
import User from '@/components/User'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/callservice',
      name: 'Service',
      component: Service
    },
    {
      path: '/bootstrap',
      name: 'Bootstrap',
      component: Bootstrap
    },
    {
      path: '/board',
      name: 'Board',
      component: Board
    },
    {
      path: '/card/:id',
      name: 'Card',
      component: Card,
      props: true
    },
    {
      path: '/user/:id',
      name: 'User',
      component: User,
      props: true
    }
  ]
})



