<template>
  <div>
    <h1>Hello! C-{{ $route.params.id }}</h1>
    <h2>{{card}}</h2>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Card',
  props: ['id'],
  data () {
    return {
      card: []
    }
  },
  beforeRouteEnter (to, from, next) {
    // this.$http.get(`api/hello`)
    axios.get('/api/statuses')
      .then((res) => {
        console.log(res.card)
        console.log(res.data)
        console.log(res.body)
        console.log(res.bodyText)
        next(vm => {
          vm.card = res.data
        })
      })
      .catch(() => {
        next('/404')
      })
  }
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }
</style>



